<template>
<div class="top mainArea page" style="padding:10px; text-align:left">
  <p class="font_xl">共通</p>
  <span v-for="(categoryData, index) in categoryListFil('CUSTOM')" :key="index">{{ categoryData.category_name_ja }}（
    <span v-on:click="pushCategory(0, categoryData.category_id)" class="link">Tier</span>
    &nbsp;/&nbsp;
    <span v-on:click="pushCategory(1, categoryData.category_id)" class="link">Alignment</span>
    ）</span>

  <div class="sp_line_xl" />
  
  <p class="font_xl">GAME</p>
  <p v-for="(categoryData, index) in categoryListFil('GAME')" :key="index" style="cursor:pointer;"
    >{{ categoryData.category_name_ja }}（
    <span v-on:click="pushCategory(0, categoryData.category_id)" class="link">Tier</span>
    &nbsp;/&nbsp;
    <span v-on:click="pushCategory(1, categoryData.category_id)" class="link">Alignment</span>
    ）</p>

  <div class="sp_line_xl" />

  <p class="font_xl">SPORTS</p>
  <p class="font_l">BASE BALL</p>
  <p v-for="(categoryData, index) in categoryListFil('BASEBALL')" :key="index" style="cursor:pointer;">{{ categoryData.category_name_ja }}（仮）</p>
  
  <div class="sp_line_l" />

  <p class="font_l">SOCCOER</p>
  <p v-for="(categoryData, index) in categoryListFil('SOCCER')" :key="index" style="cursor:pointer;">{{ categoryData.category_name_ja }}（仮）</p>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Top',
  components: {
  },
  data() {
    return {
      categoryList : [],
    };
  },
  created : function () {
  },
  computed : {
    categoryListFil : function () {
      return (kbn) => {
        if (kbn == "CUSTOM") {
          return this.categoryList.filter(e => e.class === kbn);
        } else if (kbn == "GAME") {
          return this.categoryList.filter(e => e.class === kbn);
        } else if (kbn == "BASEBALL") {
          return this.categoryList.filter(e => e.class_sub === kbn);
        } else if (kbn == "SOCCER") {
          return this.categoryList.filter(e => e.class_sub === kbn);
        }
      }
    }
  },
  mounted () {
  },
  methods : {
    pushCategory : function (kbn, catId) {
      //対象のデータを探す
      let targetCategory = null;
      for (let i = 0; i < this.categoryList.length; i++) {
        const element = this.categoryList[i];
        if (catId == element.category_id) {
          targetCategory = element;
          break;
        }
      }

      //カテゴリーのタイプで遷移先を変える
      if (targetCategory.type == "TIER" || targetCategory.type == "TIER/POSITION") {
        if (kbn == 0) {
          this.$router.push("/tier/" + targetCategory.category_id);
        } else if (kbn == 1) {
          this.$router.push("/alignment/" + targetCategory.category_id);
        }
      } else if (targetCategory.type == "POSITION") {
        this.$router.push("/position/" + targetCategory.category_id);
      }
    },
    initAction : function () {
      let params = new URLSearchParams();
      // params.append('order', order);
      // params.append('keyword', keyword);
      // params.append('page', 0);

      axios.get(this.$root.SERVER_PATH + "getCategoryList", params)
      .then((response) => {
        let resData = response.data;
        console.log(resData);
        this.categoryList = resData["categoryList"];

        let v = this.categoryList.filter(e => e.class === 'GAME');
        console.log(v);
      })
      .catch((e) => {
        alert(e); 
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
}
</script>

