<template>
<div class="alignment">
  <br>
  <div class="row">
    <div class="col-6" style="text-align:left">
      <span class="btnBase font_m" style="" v-on:click="pushBtn('BACK')">戻る</span>
    </div>
    <div class="col-6" style="text-align:right">
      <span class="btnBase font_m" style="" v-on:click="pushBtn('TIER')">ティア表はこちら</span>
    </div>
  </div>
  <br>
  <div v-show="isEditTitle" style="display:flex; justify-content:center">
    <input type="text" style="width:60%; height:60px" class="form-control bo2 font_l" v-model="title" maxlength="30" placeholder="タイトルを入力してください">
    <span class="btnBase font_m" style="" v-on:click="pushTitleBtn('OK')">OK</span>
  </div>
  <div v-show="!isEditTitle">
    <span class="font_l">{{ title }}</span>
    <span :class="editBtnClass" class="btnBase font_m" style="margin-left:10px" v-on:click="pushTitleBtn('EDIT')">編集</span>
  </div>
  <div id="chartParent" class="chartWidth" :style="'height:' + chartHeight + 'px; background-color:black; margin:auto; position:relative'">
    <div style="position:absolute; left:0; top:50%; width:100%; height:2px; background-color:white; z-index:999" />
    <div style="position:absolute; left:50%; top:0; width:2px; height:100%; background-color:white; z-index:999" />
    <input type="text" v-model="columnT" class="align-text" style="left:50%; top:2%; transform: translate(-50%, 0); text-align:center;" placeholder="input text">
    <input type="text" v-model="columnL" class="align-text" style="left:2%; top:50%; transform: translate(0, 0); text-align:left;" placeholder="input text">
    <input type="text" v-model="columnR" class="align-text" style="left:98%; top:50%; transform: translate(-100%, 0); text-align:right;" placeholder="input text">
    <input type="text" v-model="columnB" class="align-text" style="left:50%; top:98%; transform: translate(-50%, -100%); text-align:center;" placeholder="input text">
    <!-- <div style="width:100px; height:100px; background-color:blue; position: absolute; left: 120px; top:0px"
    @mousedown="touchStart($event)" @touchstart="touchStart($event)"
    @touchmove="touchMove($event)" @mousemove="touchMove($event)"
    @mouseup="touchEnd($event)" @touchend="touchEnd($event)"
    @mouseleave="touchLeave($event)" @touchleave="touchLeave($event)"/> -->
  </div>
  <div v-show="this.isCustom">
    ■テキストを追加
    <div>
      <input type="text" placeholder="アイテム名を入力" v-model="customText">
      <span class="btnBase font_l" style="margin-left:10px" v-on:click="pushBtn('ADD_TEXT')">追加</span>
    </div>
    <div class="sp_line_m" />
    ■画像を追加（複数選択可）
    <div>
      <input type="file" multiple accept=".jpg, .png" @change="onFileChange">
    </div>
    <div class="sp_line_m" />
  </div>
  <div style="height:10px" />
  <span v-show="targetItem != null && targetItem.isCustom == true" class="btnBase font_l" style="" v-on:click="pushBtn('DELETE_ITEM')">選択中のアイテムを削除</span>
  <div class="sp_line_s" />
  <span class="btnBase font_l" style="width:30%;" v-on:click="pushEditBtn('BACK')">戻す</span> &nbsp;&nbsp;
  <span class="btnBase font_l" style="width:30%;" v-on:click="pushEditBtn('BACK_ALL')">全て戻す</span>
  <div style="height:30px" />
  <span class="btnBase font_l" style="" v-on:click="pushBtn('SAVE_SCREEN')">保存</span>&nbsp;&nbsp;
  <span class="btnBase font_l" style="" v-on:click="pushBtn('SHARE')">ツイッターにシェア</span>
  <div class="sp_line_ss" />
  ※画像を添付したい場合は、一度保存して、シェアボタンから画像を添付してください。
  <div style="height:30px" />
  <div id="baseParent" style="background-color:white; padding:10px; display:flex; flex-wrap:wrap">
    <!-- <img v-for="(item, index) in baseItems" :key="index"
      :style="'width:' + itemWidth + 'px; height: ' + itemHeight + 'px; z-index:1000; left:' + item.left + '; top:' + item.top + ''"
      :src="this.$root.IMG_PATH + categoryData.category_id + '/' + item.path" :id="item.id" :class="itemClass(item)"
      @mousedown.prevent="touchStart($event)" @touchstart.prevent="touchStart($event)"
      @touchmove.prevent="touchMove($event)" @mousemove.prevent="touchMove($event)"
      @mouseup.prevent="touchEnd($event)" @touchend.prevent="touchEnd($event)"
      @mouseleave.prevent="touchLeave($event)" @touchleave.prevent="touchLeave($event)"> -->
    <img v-for="(item, index) in baseItems" :key="index"
      :style="'width:' + itemWidth(item.scale) + 'px; height: ' + itemHeight + 'px; z-index:1000; left:' + item.left + '; top:' + item.top + ''"
      :src="item.path" :id="item.tierNo" :class="itemClass(item)" @mousedown.prevent="touchStart($event)" @touchstart.prevent="touchStart($event)" @touchmove.prevent="touchMove($event)" @touchend.prevent="touchEnd($event)">
    <span v-for="(item, index) in baseTextItems" :key="index" :style="itemTextFont + '; z-index:1000; left:' + item.left + '; top:' + item.top" :id="item.tierNo" class="cTierText" :class="itemClass(item)" @mousedown.prevent="touchStart($event)" @touchstart.prevent="touchStart($event)" @touchmove.prevent="touchMove($event)" @touchend.prevent="touchEnd($event)">{{ item.path }}</span>
  </div>
</div>

<!-- スクショ用 -->
<div id="screen" style="position:absolute; left:3000px; top:10px; background-color:#b8e994">
  <p class="font_l" style="text-align:center">{{ title }}</p>
  <div style="width:1200px; height:600px; background-color:black; margin:auto; position:relative">
    <div style="position:absolute; left:0; top:50%; width:100%; height:2px; background-color:white; z-index:999" />
    <div style="position:absolute; left:50%; top:0; width:2px; height:100%; background-color:white; z-index:999" />
    <input type="text" v-model="columnT" class="align-text" style="left:50%; top:2%; transform: translate(-50%, 0); text-align:center;" placeholder="input text">
    <input type="text" v-model="columnL" class="align-text" style="left:2%; top:50%; transform: translate(0, 0); text-align:left;" placeholder="input text">
    <input type="text" v-model="columnR" class="align-text" style="left:98%; top:50%; transform: translate(-100%, 0); text-align:right;" placeholder="input text">
    <input type="text" v-model="columnB" class="align-text" style="left:50%; top:98%; transform: translate(-50%, -100%); text-align:center;" placeholder="input text">
    <img v-for="(item, index) in baseItems" :key="index" :class="itemClass(item)"
      :style="'width:' + itemSSWidth(item.scale) + 'px; height: 120px; position:absolute; display:' + ssDisp(item.isBase) + '; z-index:1000; left:' + item.left + '; top:' + item.top + ''" :src="item.path" :id="item.tierNo">
    <span v-for="(item, index) in baseTextItems" :class="itemClass(item)" :key="index" :style="itemSSTextFont + '; position:absolute; display:' + ssDisp(item.isBase) + '; z-index:1000; left:' + item.left + '; top:' + item.top" :id="item.tierNo" class="cTierText">{{ item.path }}</span>
  </div>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Alignment',
  components: {
  },
  data() {
    return {
      x : 0,
      y : 0,
      isTouch : false,
      targetEl : null,
      targetItem : null,
      baseItems : [],
      baseTextItems : [],

      chartParentWidth : 1000,

      customText : "",

      title : "タイトル未設定",
      isEditTitle : false,

      hScale : 1,
      isCustom : false,
      tierNo : 0,

      columnT : "",
      columnR : "",
      columnB : "",
      columnL : "",
    };
  },
  computed : {
    ssDisp : function() {
      return (isBase) => {
        if (!isBase) {
          return "flex";
        } else {
          return "none";
        }
      }
    },
    itemClass : function () {
      return (item) => {
        let retVal = "unselectAItem ";
        if (this.targetItem != null && item.tierNo == this.targetItem.tierNo) {
          retVal = "selectItem ";
        }
        if (item.isBase) {
          return retVal + "itemBase";
        } else {
          return retVal + "itemChart";
        }
      }
    },
    itemBaseWidth : function () {
      return this.itemHeight / this.hScale;
    },
    itemWidth : function() {
      return (scale) => {
        if (scale == 0) {
          return this.itemBaseWidth;
        } else {
          return this.itemHeight / scale;
        }
      }
    },
    itemTextFont : function () {
      let val = this.chartParentWidth * 0.02;
      return "padding: 0 " + val + "px; font-size: " + val + "px; min-height: " + (val * 3) + "px; ";
    },
    itemSSTextFont : function () {
      let val = 1200 * 0.02;
      return "padding: 0 " + val + "px; font-size: " + val + "px; min-height: " + (val * 3) + "px; ";
    },
    itemSSWidth : function() {
      return (scale) => {
        if (scale == 0) {
          return 120 / this.hScale;
        } else {
          return 120 / scale;
        }
      }
    },
    itemHeight : function() {
      let val = this.chartParentWidth * 0.1;
      // if (val > 100) {
      //   val = 100;
      // }
      return val;
    },
    chartHeight : function () {
      return this.chartParentWidth / 2;
    }
  },
  mounted () {
    this.$nextTick(function () {
      // ビュー全体がレンダリングされた後にのみ実行されるコード
      let baseParent = document.getElementById("baseParent");
      let chartParent = document.getElementById("chartParent");

      let baseList = Array.from(document.getElementsByClassName("itemBase"));
      let chartList = Array.from(document.getElementsByClassName("itemChart"));
      console.log(baseList);
      console.log(chartList);

      for (let i = 0; i < baseList.length; i++) {
        const element = baseList[i];
        baseParent.appendChild(element);
      }
      for (let i = 0; i < chartList.length; i++) {
        const element = chartList[i];
        chartParent.appendChild(element);
      }
    });

    this.chartParentWidth = document.getElementById("chartParent").clientWidth;
    window.addEventListener('resize', this.calculateWindowWidth);

    window.addEventListener('mousemove', this.touchMove);
    window.addEventListener('mouseup', this.touchEnd);
  },
  beforeRouteLeave(to, from, next) {
    //イベントリスナー解除
    window.removeEventListener('resize', this.calculateWindowWidth);
    window.removeEventListener('mousemove', this.touchMove);
    window.removeEventListener('mouseup', this.touchEnd);
    next();
  },
  methods : {
    calculateWindowWidth : function () {
      this.chartParentWidth = document.getElementById("chartParent").clientWidth;
      console.log(this.chartParentWidth);
    },
    onFileChange : function (e) {
      const files = e.target.files || e.dataTransfer.files;
      console.log(files);

      if (files.length == 0) {
        return;
      }

      for (let i = 0; i < files.length; i++) {
        const element = files[i];

        //形式
        if (element.type == "image/jpeg" || element.type == "image/png") {
          this.createImage(element);
        }
      }
    },
    createImage(file) {
      const reader = new FileReader();
      reader.onload = e => {
        //画像の縦横を取得
        let img = new Image();
        img.src = e.target.result;
        img.onload = function() {
          let width = img.naturalWidth;
          let height = img.naturalHeight;
          let scale = height / width;

          this.baseItems.push({tierNo:this.tierNo, path:e.target.result, isCustom: true, scale:scale, left:"0px", top:"0px", isBase:true});
          this.tierNo++;
          // console.log(this.baseItems);
        }.bind(this);
      };
      reader.readAsDataURL(file);
    },
    pushBtn : function (name) {
      if (name == "ADD_TEXT") {
        if (this.customText == "") {
          return;
        }
        this.baseTextItems.push({tierNo:this.tierNo, path:this.customText, isCustom: true, scale:0, left:"0px", top:"0px", isBase:true});
        this.tierNo++;
      } else if (name == "BACK") {
        this.$router.push("/");
      } else if (name == "TIER") {
        this.$router.push("/tier/" + this.categoryData.category_id);
      } else if (name == "DELETE_ITEM") {
        //対象のID
        let tTierNo = this.targetItem.tierNo;
        console.log(this.targetItem);
        for (let i = 0; i < this.baseItems.length; i++) {
          const element = this.baseItems[i];
          if (element.tierNo == tTierNo) {
            console.log(element.path);
            this.baseItems.splice(i, 1);
            this.targetItem = null;
            return;
          }
        }

        for (let i = 0; i < this.baseTextItems.length; i++) {
          const element = this.baseTextItems[i];
          if (element.tierNo == tTierNo) {
            console.log(element.path);
            this.baseTextItems.splice(i, 1);
            this.targetItem = null;
            return;
          }
        }
      } else if (name == "SAVE_SCREEN") {
        this.targetItem = null;

        setTimeout(() => {
          //画像化
          window.html2canvas(document.getElementById('screen'), { 
            scale:1
          }).then(canvas => {
            console.log(canvas.width + " " + canvas.height);
            // var base64 = canvas.toDataURL('image/jpeg');
            // base64 = base64.replace(/^.*,/, '');

            const a = document.createElement("a");
            a.href = canvas.toDataURL("image/jpeg", 1);
            a.download = "image.jpg";
            a.click();

            // axios.post(this.$root.SERVER_PATH + "updateChallenge", params);      
          });
        }, "500");
      } else if (name == "SHARE") {
        let bodyStr = "【とりとりメーカー】" + this.title;
        let siteUrl = "http://www.yahoo.co.jp";
        var url = "https://twitter.com/intent/tweet?" + 
        "hashtags=" + encodeURIComponent('toritori') + 
        "&text=" + encodeURIComponent(bodyStr) + 
        "&url=" + encodeURIComponent(siteUrl);
        // "&via=" + encodeURIComponent("iphonetaro_live");
        window.open(url);
        this.targetItem = null;
      }
    },
    test : function() {
      console.log(document.getElementById("1"));
    },
    pushTitleBtn : function (kbn) {
      if (kbn == "EDIT") {
        this.isEditTitle = true;
      } else if (kbn == "OK") {
        //保存
        this.title = this.title.trim();
        if (this.title == "") {
          this.title = "タイトル未設定";
        }

        // this.onEnd();
        this.isEditTitle = false;
      }
      this.targetItem = null;
    },
    pushEditBtn : function(name) {
      if (name == "BACK") {
        if (this.targetItem == null) {
          return;
        }

        document.getElementById("baseParent").append(this.targetEl)
        this.targetItem.isBase = true;
        this.targetEl = null;
        this.targetItem = null;
      } else if (name == "BACK_ALL") {
        let chartList = Array.from(document.getElementsByClassName("itemChart"));
        let baseParent = document.getElementById("baseParent");
        for (let i = 0; i < chartList.length; i++) {
          const element = chartList[i];
          baseParent.appendChild(element);
        }
        for (let i = 0; i < this.baseItems.length; i++) {
          this.baseItems[i].isBase = true;
        }
        for (let i = 0; i < this.baseTextItems.length; i++) {
          this.baseTextItems[i].isBase = true;
        }
        this.targetEl = null;
        this.targetItem = null;
      }
    },
    mouseMove : function(e) {
      if (!this.isTouch) {
        return;
      }

      console.log(e);
    },
    mouseUp : function() {
      this.isTouch = false;
    },
    touchStart : function(e){
      // console.log(e);
      this.targetEl = e.target;
      // console.log(this.targetEl);
      
      var event = null;
      if(e.type == "mousedown") {
        event = e;
      } else if (e.type == "touchstart") {
        event = e.touches[0];
      }
      
      // console.log(event.pageX + " " + this.targetEl.offsetLeft);
      this.x = event.pageX - this.targetEl.offsetLeft;
      this.y = event.pageY - this.targetEl.offsetTop;

      let id = e.target.id;
      for (let i = 0; i < this.baseItems.length; i++) {
        if (id == this.baseItems[i].tierNo) {
          this.targetItem = this.baseItems[i];
          break;
        }        
      }
      for (let i = 0; i < this.baseTextItems.length; i++) {
        if (id == this.baseTextItems[i].tierNo) {
          this.targetItem = this.baseTextItems[i];
          break;
        }        
      }
      this.isTouch = true;
    },
    touchMove : function(e){
      if (!this.isTouch) {
        return;
      }
      if (this.targetItem.isBase) {
        //ベースから移行
        this.targetItem.isBase = false;
        document.getElementById("chartParent").appendChild(this.targetEl);
        var clientRect = document.getElementById("chartParent").getBoundingClientRect() ;
        var x = window.pageXOffset + clientRect.left;
        this.x = x + 50;

        var y = window.pageYOffset + clientRect.top;
        this.y = y + 50;
        // console.log(this.targetEl.style.top + " " + this.targetEl.style.left);
        // console.log(e);
        // this.x = window.innerWidth / 2;
        // this.y = 75;
      }

      var event = null;
      if(e.type == "mousemove") {
        event = e;
      } else if (e.type == "touchmove") {
        event = e.touches[0];
      }
      let top = event.pageY - this.y;
      let left = event.pageX - this.x;

      let cWidth = document.getElementById('chartParent').clientWidth;
      let cHeight = document.getElementById('chartParent').clientHeight;

      let tWidth = this.targetEl.clientWidth;
      let tHeight = this.targetEl.clientHeight;
      console.log(left + " " + cWidth + " " + tWidth + " " + (cWidth - tWidth));

      if (top < 0) {
        this.targetItem.top = (-2 / cHeight * 100) + "%";
      } else if (top > cHeight - tHeight - 2) {
        this.targetItem.top = ((cHeight - tHeight - 4) / cHeight * 100) + "%";
      } else {
        this.targetItem.top = (top / cHeight * 100) + "%";
      }

      if (left < 0) {
        this.targetItem.left = (-2 / cWidth * 100)  + "%";
      } else if (left > cWidth - tWidth - 2) {
        console.log("in");
        this.targetItem.left = ((cWidth - tWidth - 4) / cWidth * 100)  + "%";
      } else {
        this.targetItem.left = (left / cWidth * 100) + "%";
      }
    },
    touchEnd : function(){
      if (!this.isTouch) {
        return;
      }
      console.log("touchEnd");
      // console.log("touch end:%d,%d", e.offsetX, e.offsetY);
      this.isTouch = false;
    },
    touchLeave : function(){
      if (!this.isTouch) {
        return;
      }
      console.log("touchLeave");
      // console.log("touch leave:%d,%d", e.offsetX, e.offsetY);
      this.isTouch = false;
    },
    initAction : function() {
      let catId = this.$route.params.catId;
      if (catId == "tier_custom") {
        //カスタム
        this.isCustom = true;
        this.baseItems = [];
        this.baseTextItems = [];
        return;
      }

      //データ取得
      let params = new URLSearchParams();
      params.append('categoryId', this.$route.params.catId);

      axios.post(this.$root.SERVER_PATH + "getItemData", params)
      .then((response) => {
        let resData = response.data;
        console.log(resData);
        
        //カテゴリー情報
        this.categoryData = resData["categoryData"];

        //アイテムデータ
        let itemList = resData["itemList"];
        this.baseItems = [];
        let isSizeCheck = false;
        for (let i = 0; i < itemList.length; i++) {
          const element = itemList[i];
          
          if (!isSizeCheck && element.item_type == "IMAGE") {
            console.log("IN");
            //カテゴリーの画像サイズを取得
            isSizeCheck = true;
            let img = new Image();
            img.onload = function() {
              var width = img.naturalWidth;
              var height = img.naturalHeight;
              console.log("IMG SIZE " + width + " " + height);
              this.hScale = height / width;
              console.log(this.hScale);
            }.bind(this);
            var path = this.$root.IMG_PATH + this.categoryData.category_id + "/" + element.item_name + '.png';
            console.log(path);
            img.src = path; 
          }
          this.baseItems.push({tierNo:element.item_id, path:this.$root.IMG_PATH + this.categoryData.category_id + "/" + element.item_name + '.png', isCustom: false, scale:0, left:"0px", top:"0px", isBase:true,});
        }
      })
      .catch((e) => {
        alert(e); 
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
}
</script>

<style>
.align-text {
  position:absolute;
  background-color:transparent;
  color : white;
  z-index:1001;
  border : none;
}



.itemChart {
  position : absolute;
}

.itemBase {
  position : static;
}

.chartWidth {
  width:95%;
}

.selectAItem {
  border : 2px solid red;
}
.unselectAItem {
  border : 2px solid transparent;
}


@media screen and (min-width : 1280px) {
  .chartWidth {
    width : 1200px;
  }
}
</style>