import Top from '../views/Top.vue'
import Tier from '../views/Tier.vue'
import Test2 from '../views/Test2.vue'
import Position from '../views/Position.vue'
import Alignment from '../views/Alignment.vue'

const routes = [
  {
    path: '/',
    name: 'Top',
    component: Top
  },
  {
    path: '/tier/:catId',
    name: 'Tier',
    component: Tier
  },
  {
    path: '/alignment/:catId',
    name: 'Alignment',
    component: Alignment
  },
  {
    path: '/position/:catId',
    name: 'Position',
    component: Position
  },
  {
    path: '/test2',
    name: 'Test2',
    component: Test2
  },
]

